import React, { useState } from 'react';
import styled, { keyframes } from 'styled-components';
import { fadeInDownBig, fadeInRight, fadeInLeft } from 'react-animations'
import '../src/app.css';
import { getData } from './apiCall/call';


const Bounce = styled.div`animation: 2s ${keyframes`${fadeInDownBig}`} `;
const Left = styled.div`animation: 2s ${keyframes`${fadeInLeft}`} `;
const Right = styled.div`animation: 2s ${keyframes`${fadeInRight}`} `;
function App() {
  var [n, setN] = useState(0);
  var [data, setData] = useState([]);
  var [check, setCheck] = useState(false);
  var [load, setLoad] = useState(false);


  function submit(e) {
    setLoad(true);
    setCheck(false);
    setData([]);
    e.preventDefault();
    console.log(n);
    getData(n, (d) => {
      setData([].concat(d));
      setLoad(false);

    });

  }
  function changeN(e) {
    var n1 = e.target.value;
    n1 = parseInt(n1);
    setN(n1);
  }

  function renFun(d) {
    if (d.freq === 0) {
      setData([]);
      setCheck(true);
      return null;
    }
    return <tr>
      <td>{d.name}</td>
      <td>{d.freq}</td>
    </tr>

  }


  return (
    <div>
      <div className="flex" style={{ marginTop: 50 }} ><Bounce><h1>Welcome!</h1></Bounce></div>
      <div className="flex" >
        <form className="form-group " noValidate onSubmit={submit} style={{ width: "80%" }}>

          <div className="flex up"> <Left><input style={{ marginTop: 10, marginRight: 10 }} type="Number" onChange={changeN} placeholder="Enter N value" name="nVal" /></Left>
            <Right> <input type="submit" value="Get" className="btn btn-lg btn-success" /></Right>  </div>
          <div className="flex"> {check ? <small style={{ color: "red" }}>Check the range of number you entered!</small> : null} {load ? <h6>Loading....</h6> : null} </div>
        </form>

      </div>
      {data.length !== 0 ?
        <div>
          < div className="flex">

            <table className="table" style={{ margin: 30 }}>
              <tr>
                <th>Word</th>
                <th>Freqency</th>
              </tr>
              {data.map(renFun)}
            </table>
          </div>
          <h4 className="flex" >Liked my work? Then do visit my website to know me more...<br /> <a href="https://atreyarao.github.io/Atreya/" rel="noopener noreferrer" target="_blank" className="btn btn-primary">Visit</a> </h4>
        </div> : null
      }

    </div >
  );
}

export default App;
